import siteSettingPage from "@/view/manngger-main/siteSettingPage";
// import {globalValue} from "@/config/globalConfig";
import apiDoc from "@/view/manngger-main/apiDoc";
import userPage from "@/view/user-admin/userPage";
import dataPage from '@/view/dataPage/dataPage.vue'
import logPage from "@/view/log/logPage";
import authPage from "@/view/auth-admin/authPage";
import tokenPage from "@/view/token-admin/tokenPage";
import rolePage from "@/view/role-admin/rolePage";
export const adminMain=[
    {
        path:'/dataPage',
        component:dataPage,
        meta:{
            showInbreadcrumb:true,
            title:'数据统计',
            mustLogin:true
        }
    },
    {
        path:'/loginfo',
        component:logPage,
        meta:{
            showInbreadcrumb:true,
            title:'日志记录',
            mustLogin:true
        }
    },
    {
        path:'/settingPage',
        component:siteSettingPage,
        meta:{
            showInbreadcrumb:true,
            title:'站点设置',
            mustLogin:true
        }
    },
    {
        path:'/apidoc',
        component:apiDoc,
        meta:{
            showInbreadcrumb:true,
            title:'API文档',
            mustLogin:true,
            mustAdmin:true
        }
    },
    {
        path:'/userPage',
        component:userPage,
        meta:{
            showInbreadcrumb:true,
            title:'用户管理',
            mustLogin:true,
            mustAdmin:true
        }
    },
    {
        path:'/rolePage',
        component:rolePage,
        meta:{
            showInbreadcrumb:true,
            title:'角色管理',
            mustLogin:true,
            mustAdmin:true
        }
    },
    {
        path:'/tokenPage',
        component:tokenPage,
        meta:{
            showInbreadcrumb:true,
            title:'Token管理',
            mustLogin:true,
            mustAdmin:true
        }
    },
    {
        path:'/authPage',
        component:authPage,
        meta:{
            showInbreadcrumb:true,
            title:'鉴权中心管理',
            mustLogin:true
        }
    }
]

